// lib/api/video-api.ts
import axios from "axios";

export interface VideoSource {
  url: string;
  quality: string;
  type: "iframe";
  servers: string;
  tmdbId: number;
  mediaType: "movie" | "tv";
}

/** Read env and trim. Allow either base URLs or URL templates. */
const ENV = {
  BASE: (process.env.NEXT_PUBLIC_VIDEO_API_BASE || "").trim(),
  E1:   (process.env.NEXT_PUBLIC_VIDEO_EMBED_1 || "").trim(),
  E2:   (process.env.NEXT_PUBLIC_VIDEO_EMBED_2 || "").trim(),
  E3:   (process.env.NEXT_PUBLIC_VIDEO_EMBED_3 || "").trim(),
  E4:   (process.env.NEXT_PUBLIC_VIDEO_EMBED_4 || "").trim(),
};

/** Providers list (order = fallback order). Empty strings are filtered out. */
const VIDEO_APIS = [
  { name: "Primary",   value: ENV.E1 },
  { name: "Secondary", value: ENV.E2 },
  { name: "Tertiary",  value: ENV.E3 },
  { name: "Backup",    value: ENV.E4 },
  { name: "Base",      value: ENV.BASE }, // optional global base
].filter(p => !!p.value);

/** Build the default embed path when provider is a base URL (not a template). */
function buildEmbedPath(
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number
) {
  if (mediaType === "movie") return `/movie/${tmdbId}`;
  if (season && episode) return `/tv/${tmdbId}/${season}/${episode}`;
  return `/tv/${tmdbId}`;
}

/** Returns true if the provider uses placeholders like ${id} */
function isTemplate(u: string) {
  return /\$\{id\}/.test(u);
}

/** Normalize base URL (remove trailing slash) */
function normalizeBase(u: string) {
  return u.endsWith("/") ? u.slice(0, -1) : u;
}

/** Build a final playable URL from a provider value (template OR base). */
export function buildEmbedUrl(
  providerValue: string,
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number
) {
  if (isTemplate(providerValue)) {
    // Template mode: replace placeholders
    // Supported placeholders: ${id}, ${type}, ${season}, ${episode}
    return providerValue
      .replace(/\$\{id\}/g, String(tmdbId))
      .replace(/\$\{type\}/g, mediaType)
      .replace(/\$\{season\}/g, season ? String(season) : "")
      .replace(/\$\{episode\}/g, episode ? String(episode) : "");
  }
  // Base mode: append our canonical path
  const base = normalizeBase(providerValue);
  return `${base}${buildEmbedPath(mediaType, tmdbId, season, episode)}`;
}

/** Convenience to get URL by provider index */
export function buildEmbedUrlFromIndex(
  providerIndex: number,
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number
) {
  const prov = VIDEO_APIS[providerIndex];
  if (!prov) return "";
  return buildEmbedUrl(prov.value, mediaType, tmdbId, season, episode);
}

function toSource(
  provName: string,
  value: string,
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number
): VideoSource {
  return {
    url: buildEmbedUrl(value, mediaType, tmdbId, season, episode),
    quality: "HD",
    type: "iframe",
    servers: provName,
    tmdbId,
    mediaType,
  };
}

/** Robust TMDB id resolver (supports numeric or IMDb tt... ids). */
export async function resolveTmdbId(
  input: string | number,
  type: "movie" | "tv"
): Promise<number | null> {
  // numeric?
  if (typeof input === "number" && Number.isFinite(input)) return input;
  const s = String(input).trim();
  const asNum = Number(s);
  if (Number.isFinite(asNum)) return asNum;

  // IMDb id?
  if (/^tt\d+$/i.test(s)) {
    const API_KEY = (process.env.NEXT_PUBLIC_TMDB_API_KEY || "").trim();
    if (!API_KEY) return null;
    try {
      const { data } = await axios.get(
        `https://api.themoviedb.org/3/find/${s}`,
        { params: { api_key: API_KEY, external_source: "imdb_id" } }
      );
      const arr = type === "movie" ? data.movie_results : data.tv_results;
      return arr?.[0]?.id ?? null;
    } catch {
      return null;
    }
  }

  return null;
}

export const videoApi = {
  hasConfiguredApis(): boolean {
    return VIDEO_APIS.length > 0;
  },

  getConfigStatus() {
    return {
      configured: VIDEO_APIS.length,
      apis: VIDEO_APIS.map(a => ({ name: a.name, configured: !!a.value })),
    };
  },

  async getMovieSources(tmdbId: number): Promise<VideoSource[]> {
    return VIDEO_APIS.map(p => toSource(p.name, p.value, "movie", tmdbId));
  },

  async getTVSources(tmdbId: number, season?: number, episode?: number): Promise<VideoSource[]> {
    return VIDEO_APIS.map(p => toSource(p.name, p.value, "tv", tmdbId, season, episode));
  },

  async getMovieWithEmbed(tmdbId: number) {
    const API_KEY = (process.env.NEXT_PUBLIC_TMDB_API_KEY || "").trim();
    try {
      const movie = API_KEY
        ? (await axios.get(`https://api.themoviedb.org/3/movie/${tmdbId}`, { params: { api_key: API_KEY } })).data
        : null;
      const sources = await this.getMovieSources(tmdbId);
      return { movie, embedUrl: sources[0]?.url || "", sources };
    } catch {
      return { movie: null, embedUrl: "", sources: [] };
    }
  },

  async getTVShowWithEmbed(tmdbId: number, season?: number, episode?: number) {
    const API_KEY = (process.env.NEXT_PUBLIC_TMDB_API_KEY || "").trim();
    try {
      const tvShow = API_KEY
        ? (await axios.get(`https://api.themoviedb.org/3/tv/${tmdbId}`, { params: { api_key: API_KEY } })).data
        : null;
      const sources = await this.getTVSources(tmdbId, season, episode);
      return { tvShow, embedUrl: sources[0]?.url || "", sources };
    } catch {
      return { tvShow: null, embedUrl: "", sources: [] };
    }
  },
};

